/***** Project Cards *****/
.blog-card {
    cursor: pointer;
    margin-bottom: 50px;
    width: 85%;
    max-width: 800px;
    margin-top: 20px;
}

/*** Header ***/
.blog-header {
    display: inline-block;
    text-decoration: none;
}

.blog-title {
    font-family: Raleway, sans-serif;
    font-size: 30px;
    line-height: 1.2;
    margin: 0;
    color: hsl(200, 100%, 30%);
    font-weight: 500;

    text-overflow: ellipsis;
}

.blog-date {
    margin-top: 10px;
    margin-bottom: 0;
    font-family: Roboto, sans-serif;
    color: hsl(200, 10%, 55%);
    font-size: 14px;
}

/*** Content ***/
a.blog-info {
    font-size: 18px;
    font-family: 'EB Garamond', serif;
    line-height: 1.5;
    text-decoration: none;
    color: hsl(200, 10%, 10%);

    display: block;
    margin-bottom: 20px;
    position: relative;
}

.blog-info img {
    display: block;
    max-width: 100%;
    margin: 10px auto;
}

.blog-info-cover {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-image: linear-gradient(to bottom, hsla(200, 10%, 95%, 0), hsl(200, 10%, 95%, 0.98) 85%, hsl(200, 10%, 95%));
    z-index: 1;
}


/* Tags */
.blog-tags {
    display: flex;
    flex-direction: row;
    margin-top: 12px;
    margin-bottom: 0;
}

.blog-tag {
    background-color: hsl(200, 10%, 90%);
    border: 1px solid hsl(200, 10%, 85%);

    color: hsl(200, 30%, 30%);
    font-size: 12px;
    padding: 2px 12px;
    letter-spacing: 0.05em;
}


/***** Media Queries *****/
@media (max-width: 600px) {
    .blog-title {
        font-size: 24px;
    }

    .blog-date {
        font-size: 12px;
    }

    .blog-tag {
        font-size: 11px;
        padding: 2px 10px;
    }

    a.blog-info {
        font-size: 16px;
    }
}


@media print {
    .blog-card {
        box-shadow: none;
        page-break-inside: avoid;
    }

    .blog-title {
        color: black;
    }

    .blog-date span {
        color: black;
    }

    .blog-tag {
        background-color: white;
        border: 1px solid black;
        box-sizing: border-box;
        color: black;
    }
}
