html, body, #root, #page-container {
  max-height: 100vh;
  max-width: 100vw;
}

#page-container {
    overflow-x: hidden;
    overflow-y: scroll;
    -webkit-overflow-scrolling: touch;
    -webkit-transform-style: preserve-3d;
            transform-style: preserve-3d;

    background-color: hsl(200, 10%, 95%);
}

body {
  margin: 0;
  padding: 0;
  font-family: Roboto, sans-serif;
  background-image: linear-gradient(hsl(200, 10%, 90%), hsl(190, 10%, 60%));
}

@media print {
    body {
        background-image: none;
    }

    #page-container {
        overflow: visible;
    }
}

/*** Navigation Bar **/
#navigation-bar {
    background-color: hsla(200, 30%, 5%, 0.85);
    -webkit-backdrop-filter: blur(5px);
            backdrop-filter: blur(5px);
    box-shadow: none;

    position: fixed;
    top: 0;
    z-index: 100;
    width: 100%;

    display: -webkit-flex;

    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;

    transition:
        background-color 0.15s ease-in-out;
}

html[data-scroll='0'] #navigation-bar {
    -webkit-backdrop-filter: none;
            backdrop-filter: none;
    background-color: transparent;
}

#main-bar {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-align-items: center;
            align-items: center;

    height: 56px;
}

#navigation-bar a {
    text-decoration: none;
}


/*** Logo ***/
#main-logo-container {
    display: block;

    margin-left: 25px;
    margin-right: 45px;
}

#main-logo {
    font-family: 'Fira Sans Condensed', sans-serif;
    color: white;

    font-size: 30px;
    line-height: 1;
    text-decoration: none;

    transition: color 0.2s ease-in-out;
}

#main-logo:hover {
    color: hsl(200, 5%, 95%);
}


/*** Tabs ***/
.navigation-tabs {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
}

.nav-tab {
    -webkit-flex: 1 1;
            flex: 1 1;

    display: -webkit-flex;

    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;

    color: white;
    font-family: 'Raleway';
    font-size: 18px;

    border-bottom: 3px solid transparent;
    transition: background-color 0.15s ease-in-out,
        border-bottom 0.15s ease-in-out;
}

html:not([data-scroll='0']) .nav-tab.selected {
    background-color: hsla(200, 10%, 90%, 0.1);
    border-bottom: 3px solid hsla(200, 90%, 50%, 0.5);
}

html:not([data-scroll='0']) .nav-tab:hover:not(.selected) {
    background-color: hsla(200, 10%, 90%, 0.05);
}

.desktop.navigation-tabs {
    width: 375px;
    height: 100%;
}

.desktop.navigation-tabs .nav-tab {
    border-top: 3px solid transparent;
}

.mobile.navigation-tabs {
    display: none;
}


/*** Social Icons ***/
#social-buttons {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;

    position: absolute;
    right: 10px;

}

#social-buttons > a {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-align-items: center;
            align-items: center;
}

.social-icon {
    width: 30px;
    margin-right: 20px;
}


/***** Media Queries *****/
@media (max-width: 700px) {
    .mobile.navigation-tabs {
        display: -webkit-flex;
        display: flex;
        -webkit-flex-direction: row;
                flex-direction: row;
        height: 40px;
    }

    html[data-scroll='0'] .mobile.navigation-tabs {
        border-bottom: 1px solid rgba(255, 255, 255, 0.3);
    }

    .desktop.navigation-tabs {
        display: none;
    }

    #main-bar {
        height: 44px;
    }

    #main-logo-container {
        margin-left: 10px;
    }

    #main-logo {
        font-size: 24px;
    }

    #social-buttons {
        right: 0;
    }

    .social-icon {
        width: 25px;
        margin-right: 10px;
    }
}

@media print {
    #navigation-bar {
        position: static;
        background-color: transparent;
    }

    #main-bar {
        height: auto;
    }

    #main-logo {
        color: black;
        font-size: 14px;
    }

    .navigation-tabs {
        display: none;
    }

    #social-buttons {
        display: none;
    }
}

.banner-container {
    left: 0;
    right: 0;

    position: relative;
    height: 450px;
    z-index: -10;
}

.banner-container.thin {
    height: 220px;
}

html {
    -webkit-perspective: 1000px;
            perspective: 1000px;
    -webkit-perspective-origin: center 225px;
            perspective-origin: center 225px;
}

.banner-background {
    width: 100%;
    height: 100%;

    /* Background Image */
    object-fit: cover;

    /* Parallax Scroll */
    z-index: -100;
    -webkit-transform: translateZ(-1000px) scale(2.1);
            transform: translateZ(-1000px) scale(2.1);
}

.banner-overlay {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    padding-top: 20px;

    display: -webkit-flex;

    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;

    background: linear-gradient(
        hsla(200, 20%, 10%, 0.3),
        hsla(200, 20%, 10%, 0.6)
    );
}

.banner-text {
    display: block;
    position: relative;

    margin: 0 40px;

    color: white;
    text-align: center;
}

.banner-text > h1 {
    font-family: 'Raleway', sans-serif;
    font-weight: 700;
    font-size: 90px;
    margin: 0;
}

.banner-container.thin .banner-text > h1 {
    font-size: 44px;
}

.banner-text > p {
    font-family: 'Raleway';
    font-size: 28px;
    line-height: 1.4;
    margin-top: 0.8em;
}

.down-arrow {
    position: absolute;
    bottom: 30px;
    display: none;
}

.down-arrow img {
    width: 50px;
}

@-webkit-keyframes down-arrow {
    from {-webkit-transform: translateY(0);transform: translateY(0);}
    to {-webkit-transform: translateY(-10px);transform: translateY(-10px);}
}

@keyframes down-arrow {
    from {-webkit-transform: translateY(0);transform: translateY(0);}
    to {-webkit-transform: translateY(-10px);transform: translateY(-10px);}
}


/* Media Queries */

@media (max-width: 600px) {
    .banner-container {
        height: 350px;
    }

    .banner-container.thin {
        height: 300px;
    }

    .banner-overlay {
        padding-top: 80px;
    }

    html {
        -webkit-perspective-origin: center 175px;
                perspective-origin: center 175px;
    }

    .banner-text > h1 {
        font-size: 54px;
    }

    .banner-container.thin .banner-text > h1 {
        font-size: 36px;
    }

    .banner-text > p {
        font-size: 20px;
    }
}

@media (max-height: 600px) {
    .banner-container:not(.thin) {
        height: 100vh;
    }

    html {
        -webkit-perspective-origin: center;
                perspective-origin: center;
    }

    .banner-container:not(.thin) .down-arrow {
        display: block;

        -webkit-animation-name: down-arrow;

                animation-name: down-arrow;
        -webkit-animation-direction: alternate;
                animation-direction: alternate;
        -webkit-animation-duration: 1s;
                animation-duration: 1s;
        -webkit-animation-iteration-count: infinite;
                animation-iteration-count: infinite;
        -webkit-animation-timing-function: ease-in-out;
                animation-timing-function: ease-in-out;
    }
}


@media print {
    .banner-container {
        height: auto;
    }

    .banner-background {
        display: none;
    }

    .banner-overlay {
        background: none;
        background-color: white;
        position: static;
        padding-top: 0;
    }

    .banner-text {
        color: black;
    }

    .banner-text > h1 {
        font-size: 48px;
    }

    .banner-text > p {
        font-size: 18px;
        margin-top: 4px;
        margin-bottom: 30px;
    }

    .down-arrow {
        display: none;
    }
}

