#content-container.blog {
    display: flex;
    flex-direction: column;
    align-items: center;
}

#blog-content {
    width: 95%;
    max-width: 700px;
    color: hsl(200, 10%, 15%);

    line-height: 1.5;
    font-family: 'EB Garamond', serif;
    font-size: 20px;
}

#blog-content .date {
    font-size: 14px;
    color: hsl(200, 10%, 40%);
    margin-top: 8px;
}

#blog-content img {
    max-width: 100%;
    margin: 25px auto;
    display: block;
}

#blog-content h2, h3, h4 {
    font-family: 'Raleway', sans-serif;
    font-weight: 600;
    color: hsl(200, 90%, 30%);
}

#blog-content ul, #blog-content ol {
    display: block;
    padding-left: 1.5em;
}

#blog-content .loading-placeholder {
    margin: 25px auto;
    text-align: center;
    color: hsl(200, 10%, 50%);
    font-size: 14px;
}


/***** Media Queries *****/
@media (max-width: 600px) {
    #blog-content {
        font-size: 18px;
    }
}
